<template>
  <div class="center" style="padding-top:21px;">
    <!-- <div class="center_breadcrumb">
        <el-breadcrumb separator=">">
            <el-breadcrumb-item>{{pageTitle}} </el-breadcrumb-item>
            <el-breadcrumb-item>课程详情 </el-breadcrumb-item>
       </el-breadcrumb>
    </div> -->
    <div class="center_content  graduation">
        <div class="graduation_head">
            <label>{{obj.name}}课程详情</label>
            <span @click="goBack" class="g_back">
                <SvgIcon icon-class="iconfanhui" size="15px"></SvgIcon>
                返回上级
            </span>
        </div>
        <div class="g_box">
            <div class="g_title">{{obj.name}}课程信息</div>
            <div class="graduation-ul">
                <ul>
                    <li>学时：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>&nbsp;&nbsp;{{obj.period}}</span></li>
                    <li>学分：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>&nbsp;&nbsp;{{obj.credit}}</span></li>
                    <li>课程负责人：<span>&nbsp;&nbsp;{{obj.teacherName}}</span></li>
                    <li>开课学期：<span>&nbsp;&nbsp;{{obj.term}}</span></li>
                    <li>课程类型：<span>&nbsp;&nbsp;{{obj.classification}}</span></li>
                    <li>考核方式及比例：<span>&nbsp;&nbsp;{{obj.examine}}</span></li><br>
                </ul>
              </div>
            </div>
            <div class="g_box">
                <div class="g_title">课程目标</div>
                <ul class="g_ul">
                    <li class="g_li" v-for="(item ,index) in goalList" :key="index">
                        <div class="g_li-lf">{{index + 1}}</div>
                        <div class="g_li-rt">
                            <p>{{item.name}}</p>
                            <span>{{item.content}}</span>
                            <ul>
                                <li v-for="(items, index) in item.pointNames" :key="index">
                                {{items}}
                                </li>
                            </ul>
                        </div>
                    </li>
                </ul> 
            </div>
            <div class="g_box">
                <div class="g_title">支撑指标点</div>
                    <ul class="g_ul">
                        <li class="g_li" v-for="(item ,index) in pointList" :key="index">
                            <div class="g_li-lf">{{item.point.slice(3)}}</div>
                            <div class="g_li-rt">
                                <p>指标点{{item.point}}</p>
                                <span>{{item.pointContent}}</span>
                                <ul>
                                    <li v-for="(items, index) in item.courseNames" :key="index">
                                    {{items}}
                                    </li>
                                </ul>
                            </div>
                        </li>
                    </ul>   
            </div>
            <div class="g_box">
                <div class="g_title"> 课程大纲</div>
                <div @click="fileupload" class="graduation-ke">
                     <SvgIcon icon-class="iconkechengdagang-word" size="60px"></SvgIcon>
                     <span>{{obj.name}}课程大纲</span>
                </div>
            </div>    
    </div>
  </div>
</template>
<script>
export default {
  data() {
      return{
          pageTitle: this.$route.query.title || '课程',
          obj:{},
          goalList:[],
          pointList:[],
          fileId:'',
          roleName:localStorage.getItem("roleName")

      }
  },
  methods:{
    goBack() {
     window.history.go(-1)
    },
    //   课程信息   /teacher/CourseInformation/course
    CourseInformation(){      
        let id = this.$route.query.id
        let schedulingId = this.$route.query.schedulingId
        let schedulingUrl=`/teacher/CourseInformation/course/${id}`
        if(schedulingId) schedulingUrl=`/teacher/CourseInformation/course/${id}/${schedulingId}`

        const url =this.roleName === '学生' ? `/teacher/CourseInformation/studentcourse/${id}`: schedulingUrl
         this.$get(url).then(res=>{
                    if(res.success == 200){
                        this.obj = res
                        this.goalList = res.goalList
                        this.pointList = res.pointList
                        if(res.fileId == null){
                            this.fileId = res.fileId
                        }else{
                            this.fileId = res.fileId
                        }
                        
                    }
                })
    },
    fileupload(){ //  课程大纲下载
        if(this.fileId == 'null' || this.fileId == null){
             this.$message('该课程没有上传课程大纲')
        }else{
            let token = localStorage.getItem('x_token')
            window.location.href= `${this.API_URL}/system/file/download/${this.fileId}?x-token=${token}`
        }
        
    }
  },
  mounted(){
      this.CourseInformation()
  },
   created() {
    this.userIdentity = localStorage.getItem("userIdentity");
  },
};
</script>
<style lang="scss">
@import "../../styles/center.scss";
    .graduation{
        // margin-top: 21px;
        .graduation_head{
            height:90px;
            background:url('../../assets/img/bg-dachengdujisuan.png') no-repeat center center;
            border-radius:10px 10px 0px 0px;
            position:relative;
            text-align:center;
            margin-bottom:20px;
            label{
                font-size:24px;
                font-weight:bold;
                color:#fff;
                line-height:86px;              
            }
           .g_back{
                display:inline-block;
                width:120px;
                height:28px;
                line-height:28px;
                text-align:center;
                border:1px solid #B0F9E0;
                border-radius:14px;
                color:#B0F9E0;
                 font-size:14px;
                 position:absolute;
                 left:30px;
                 top:27px;
                 cursor:pointer;
           }
        }
        .g_box{
             min-height:136px;
            background:#fff;
            border-radius:10px;
            margin-bottom:20px;
            padding:20px;
            .g_title{
                line-height:20px;
                font-size:20px;
                font-weight:bold;
                color:#323232;
            }
            .graduation-ul{
                padding-top: 27px;
                ul{
                    width:100%;
                    li{
                        list-style: none;
                        float: left;
                        width: 31%;
                        padding-bottom: 20px;
                        font-size:14px;
                        color:#323232;
                    }
                }
            }
            .g_ul{
                padding:20px 0;
                .g_li{
                    min-height:92px;
                    width: 100%;
                    text-align:left;
                    display:flex;
                    justify-content: space-between;
                    padding-bottom: 20px;
                    .g_li-lf{
                        height: 49px;
                        width: 49px;
                        border: 1px solid #cccccc;
                        border-radius: 50%;
                        position: relative;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-pack: center;
                        -ms-flex-pack: center;
                        justify-content: center;
                        -webkit-box-align: center;
                        -ms-flex-align: center;
                        align-items: center;
                        color: #696969;
                        font-size: 16px;
                    }
                    .g_li-lf:after{
                        content:'';
                        width:14px;
                        height:14px;
                        background:rgba(3,177,135,1);
                        border-radius:50%;
                        position:absolute;
                        left:0;
                        top:0;
                    }
                    .g_li-rt{
                        width:calc(100% - 80px);
                        p{
                                font-size:14px;
                                color:#323232;
                        }
                        span{
                            display: inline-block;
                                font-size:14px;
                                color:#323232;
                                margin:10px 0;
                        }
                        ul{
                            li{
                                padding:3px 8px;
                                border:1px solid rgba(221,221,221,1);
                                border-radius:5px;
                                font-size:14px;
                                color:#969696;
                                float:left;
                                margin-right:20px;
                                margin-bottom:10px;
                                &:hover{
                                    color:#03B187;
                                    font-weight:bold;
                                    background:rgba(3,177,135,0.1);
                                    border-color:rgba(3,177,135,1);

                                }
                            }
                        }
                    }
                }
            }
            .graduation-ke{
                display:inline-block;
                margin: 20px 39px;
                cursor:pointer;
                span{
                    display: block;
                    font-size:14px;
                    color:#323232;
                    margin-left: -30px;
                    margin-top:10px;
                }
            }
        }
        
    }
    
</style>



